@import "../../styles/sass/variables";
@import "../../styles/sass/mixins";

.padding__container {
  background: #fffffff2;
  position: sticky;
  z-index: 10;
  top: 0;
}
.header__container {
  @include paddingContainer;
  display: flex;
  align-items: center;
  height: 50px;
  z-index: 200;
  h1 {
    margin: 10px;
    color: $very-dark-blue-color;
  }
  .menu-items {
  }

  //Menu Icon
  .menuIcon {
    display: block;
    -webkit-user-select: none;
    user-select: none;
    width: 22px;
    z-index: 20;
    position: absolute;
    right: 20px;
    span {
      display: block;
      width: 100%;
      height: 3px;
      margin-bottom: 5px;
      position: relative;
      background: #cdcdcd;
      border-radius: 3px;
      z-index: 1;
      transform-origin: 4px 0px;
      transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1),
        background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), opacity 0.55s ease;
    }
    span:first-child {
      transform-origin: 0% 0%;
    }

    span:nth-last-child(2) {
      transform-origin: 0% 100%;
    }
  }
  .menuIcon:hover {
    cursor: pointer;
  }
  .menuIcon-open {
    span {
      opacity: 1;
      transform: rotate(-45deg) translate(-1px, -2px);
    }
    span:nth-last-child(2) {
      opacity: 0;
      transform: rotate(0deg) scale(0.2, 0.2);
    }
    span:nth-last-child(3) {
      transform: rotate(45deg) translate(2px, 1px);
    }
  }
  //End menuIcon
}

@media (min-width: $small-bp) {
  .header__container {
    .menuIcon {
      display: none;
    }
  }
}
